.pulse {
  width: 40px;
  height: 40px;
  margin: 0 auto;
  border: 2px solid #666;
  border-radius: 100%;
  -webkit-animation: sk-scaleout 1s infinite ease-in-out;
  animation: sk-scaleout 1s infinite ease-in-out;
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.pulse-inner {
  width: 34px;
  height: 34px;
  margin: -37px auto 0;
  border: 1px solid #083054;
  border-radius: 100%;
  -webkit-animation: sk-scaleout 1s infinite ease-in-out;
  animation: sk-scaleout 1s infinite ease-in-out;
}

@-webkit-keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}

@keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}

.loading-balls {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0;
}

.loading-balls li {
  height: 0;
  position: absolute;
  top: 50%;
  left: 0;
  width: 0;
  margin: 0;
  height: 10px;
  width: 10px;
  border: 3px solid #2b8ccd;
  border-radius: 100%;
  transform: transformZ(0);
  animation: LOADING 2s infinite;
}

.loading-balls li:nth-child(1n) {
  left: -20px;
  animation-delay: 0s;
}

.loading-balls li:nth-child(2n) {
  left: 0;
  animation-delay: 0.2s;
}

.loading-balls li:nth-child(3n) {
  left: 20px;
  animation-delay: 0.4s;
}

@keyframes LOADING {
  0% {
    transform: scale(0.5);
    background: #2b8ccd;
  }
  50% {
    transform: scale(1);
    background: white;
  }
  100% {
    transform: scale(0.5);
    background: #2b8ccd;
  }
}

@keyframes LOADINGREV {
  0% {
    transform: scale(0.5);
    background: white;
  }
  50% {
    transform: scale(1);
    background: #2b8ccd;
  }
  100% {
    transform: scale(0.5);
    background: white;
  }
}

