  border-color: rgba(255, 255, 255, 0);
  border-bottom-color: #fff;
  border-width: 5px;
  margin-left: -5px;
}

.switcher-group:before {
  border-color: rgba(216, 220, 227, 0);
  border-bottom-color: #d8dce3;
  border-width: 6px;
  margin-left: -6px;
}

.switcher-group i {
  display: block;
  margin: 6px 0 4px;
}

.switcher-group i:hover {
  cursor: pointer;
}

.drag-column-options {
  position: fixed;
  bottom: 20px;
  right: 20px;
  height: 80px;
  width: 100px;
  max-width: 100px;
  min-width: 30px;
  border-radius: 3px;
  opacity: 0;
  transform: translateY(100px);
  transition: opacity 0.2s ease-in, transform 0.2s ease-out;
  background: #ffffff;
  box-shadow: 0 0 9px 0px #a2a2a2;
}

.drag-column-options.active {
  transition: opacity 0.2s ease-in, transform 0.2s ease-out;
  opacity: 1;
  transform: translateY(0px);
}

.drag-column-options > .drag-column-header {
  display: none;
}

.drag-column-options .drag-inner-list,
.drag-column-options .ps-container {
  width: 100px;
  background: transparent;
}

.drag-column-options .drag-inner-list li,
.drag-column-options .drag-options {
  display: none;
}

.drag-column-options .drag-inner-list:before,
.drag-column-options .drag-inner-list:empty:before {
  content: "\f4c4";
  font-size: 63px;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  font-family: "Ionicons";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: transparent;
  opacity: 0.7;
  border: 0;
  box-shadow: none;
  height: 80px;
  width: 100px;
  text-align: center;
  padding: 0;
  margin: 0;
  padding-top: 10px;
  color: #ed5664;
}

@keyframes wobble {
  0% {
    transform: rotate(-2deg);
  }
  50% {
    transform: rotate(2deg);
  }
  100% {
    transform: rotate(-2deg);
  }
}

