/* Vue Transitions */
/* fade-slide-down */
.fade-slide-down-enter-active,
.fade-slide-down-enter-active i,
.fade-slide-down-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 1.1, 0.91, 0.9);
}

.fade-slide-down-enter,
.fade-slide-down-leave-to {
  opacity: 0;
  transform: translate(0, -8px);
}

.fade-slide-down-enter-to {
  opacity: 1;
}

/* End fade-slide-down */
/* List */
.list-enter-active,
.list-leave-active {
  transition: all 0.3s ease-in-out;
}

.list-enter,
.list-leave-to {
  opacity: 0;
}

.list-enter,
.list-leave-to {
  transform: translateY(-30px);
}

/* End List */
